<template>
  <div id="app">
    <Slideout
      menu="#menu"
      panel="#panel"
      :toggleSelectors="['.toggle-button']"
      @on-open="open"
      @on-close="close"
      @on-translatestart="open"
      @on-translate="running"
      @on-translateend="close"
    >
      <nav id="menu">
        <div>Menu</div>
      </nav>

      <main id="panel" :style="customStyle">
        <header>
          <div>
            <button class="toggle-button">☰</button>
            Panel
          </div>
        </header>
      </main>
    </Slideout>
    <div class="asideNav" :style="navStyle">Aside</div>
  </div>
</template>
<script>
import Slideout from "vue-slideout";
const width = 100; //here you can use collapse width
const extendWidth = 300; //here you can use extend width

export default {
  name: "app",
  components: {
    Slideout,
  },
  methods: {
    open: function () {
      this.navStyle.width = this.extendWidth + "px";
      this.customStyle.transform = "translateX(0px) !important";
      this.customStyle["padding-left"] = this.extendWidth + "px";
    },
    running: function () {
      this.customStyle.transform = "translateX(0px) !important";
    },
    close: function () {
      this.customStyle.transform = "translateX(0px)";
      this.customStyle["padding-left"] = this.width + "px";
      this.navStyle.width = this.width + "px";
    },
  },
  data() {
    return {
      width,
      extendWidth,
      customStyle: {
        transform: "translateX(0px)",
        "padding-left": width + "px",
      },
      navStyle: { width: width + "px" },
    };
  },
};
</script>

<style>
body {
  width: 100%;
  height: 100%;
  margin: 0;
  background-color: #4b5;
}

.slideout-menu {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 0;
  height: 100vh;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  z-index: 0;
  display: none;
  background-color: #1d1f20;
  color: white;
}

.asideNav {
  position: fixed;
  top: 0;
  bottom: 0;
  height: 100vh;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  z-index: 0;
  display: block;
  background-color: #1d1f20;
  color: white;
  z-index: 1;
  transition: width 0.1s ease-in-out;
}

.slideout-menu-left {
  left: 0;
}

.slideout-menu-right {
  right: 0;
}

.slideout-panel {
  background-color: #4b5;
  color: white;
  position: relative;
  z-index: 1;
  will-change: transform;
  min-height: 100vh;
}

.slideout-open,
.slideout-open body,
.slideout-open .slideout-panel {
  overflow: hidden;
}
</style>
